}

/* Remove underline from React Router Link */
.link {
  text-decoration: none;
}


.top_bar_decoration {
  height:10px;
  background-color:rgb(1, 54, 139); /* change color to blue */
  margin-top:0;
}

/* Header Container */
.grid-container1 {
  border-color: brown;
  /* border-style: solid;; */
  width:70%;
  min-height:10vh;
  margin:auto;
  display: grid;
  grid-template-columns: 16.6% 16.6% 16.6% 16.6% 16.6% 16.6%;
  grid-template-rows: 50% 50%;
  gap: 0px;
}


.tmulogo {
  grid-row:1/3;
  grid-column:1/2;
  height:100%;
  width:80%;
}

.logo_home {
  width:100%;
  height:100%;
}

